<template>
    <div class="f5">
        <van-nav-bar title="我的记录"/>
            <van-list
                v-model="loading"
                :finished="finished"
                :immediate-check="false"
                finished-text="没有更多了"
                @load="onLoad"
                :offset="10"
            >
            <van-cell-group v-for="(item,index) in repairlist" :key="index">
                    <van-cell title="报修房间" :value="item.room_num" />
                    <van-cell title="报修类型" :value="item.type" />
                    <van-cell title="报修项目" :value="item.room_repair_type" />
                    <van-cell title="报修状态" :value="item.repair_state" />
                    <van-cell title="报修时间" :value="item.repair_date" />
                    <van-cell title="处理时间" :value="item.maintain_date" />
                    <van-cell title="备注" :value="item.message" />
                    <van-cell  title="图片">
                        <van-button  size="small" type="info" style="margin:0 auto" @click="getimg(item.img)">查看图片</van-button>
                    </van-cell>
                </van-cell-group>
            </van-list>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
import {apirepair_list} from "@/api/index"
export default {
    name:'repairHistory',
    data() {
        return {
            loading: false,
            finished: false,
            page: 1, //请求第几页
            pageSize: 5, //每页请求的数量
            total: 0, //总共的数据条数
            repairlist: []
        }
    },
    methods: {
        // 上拉加载
        onLoad() {
            this.page++;
            this.getlist();
        },
        getlist(){
            let params = {
                page: this.page,
                pageSize: this.pageSize
            };
            apirepair_list(params).then(res=>{
                this.loading = false;
                this.total = res.data.data.count; 
                let rows = res.data.data.info
                if (res.data.data.info == null || rows.length === 0) {
                // 加载结束
                this.finished = true;
                    return;
                }
                this.repairlist = this.repairlist.concat(rows);
                this.isLoading = false;
                if (this.repairlist.length >= this.total) {
                    this.finished = true;
                }
            })
        },
        getimg(imgarr){
          if(imgarr.length==0){
                 Toast("暂无图片")
            }else{
                ImagePreview(imgarr);
            }  
        }
    },
    created(){
        this.getlist()
    }
}
</script>

<style>
.van-cell__value{
    flex: 3;
}
</style>


